<template>
  <div>
    <div class="pageMain">
      <van-cell-group
        class="contentBox"
        v-for="(item, index) in list"
        :key="index"
      >
        <van-cell
          :title="
            item.arMode == 0 ? '离场时间' : item.arMode == 1 ? '入场时间' : ''
          "
        >
          <template>
            <div>
              {{ getLocalTime(item.arDatetime) }}
            </div>
          </template>
        </van-cell>
      </van-cell-group>
      <van-empty v-if="list.length == 0" description="暂无考勤信息" />
    </div>
  </div>
</template>

<script>
export default {
  name: "Attendance",
  data() {
    return {
      list: [],
    };
  },
  methods: {
    kqDetail() {
      let projectId = JSON.parse(localStorage.getItem("project")).projectid;
      this.$http.jsb.kqDetail({ projectId }).then((res) => {
        if (res.code == 200) {
          this.list = res.data;
        }
      });
    },
    s0(str){
      return ((str.toString().length<2)
          ? "0" + (str)
          : str);
    },
    getLocalTime(nS) {
      var date = new Date(nS);
      let Y = date.getFullYear() + "-";
      let M =
        (date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1) + "-";
      let D = this.s0(date.getDate()) + " ";
      let h = this.s0(date.getHours()) + ":";
      let m = this.s0(date.getMinutes()) + ":";
      let s = this.s0(date.getSeconds());
      return Y + M + D + h + m + s;
    },
  },
  created() {
    this.kqDetail();
  },
};
</script>

<style lang="less" scoped>
.van-cell__value {
  display: inline-table;
}
</style>